import React from 'react';
import { Container } from '@material-ui/core';
import Page from 'src/components/Page';
import CreateParcelRule from './CreateParcelRule';
import { generalStyles } from '../../utils/GeneralStyle';
import { useParams } from 'react-router';

const ParcelRuleForm = () => {
  const classes = generalStyles();
  const supplierId = useParams();

  return (
    <Page className={classes.root} title={supplierId ? 'Eidt Parcel Rule' : ' Create Parcel Rule'}>
      <Container maxWidth={false}>
        <Container>
          <CreateParcelRule />
        </Container>
      </Container>
    </Page>
  );
};

export default ParcelRuleForm;
