import React, { useEffect, useState } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  Switch,
  TextField,
  Typography,
} from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import SupplierService from 'src/services/supplier';
import { SettingService } from 'src/services/setting';
import Select from 'src/views/utils/Select';
import moment from 'moment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
  switch: {
    margin: 'auto',
  },
}));

const CreateDelivery = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const { supplierId } = useParams();

  const [loading, setLoading] = useState(false);

  const [noRule, setNoRule] = useState(false);
  const [initialValues, setInitialValues] = useState({
    S: [],
    M: [],
    L: [],
    XL: [],
    XXL: [],
    XXXL: [],
  });

  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await SupplierService.findPackSizeRuleBySupplierId({ supplierId: id });

        if (!result.content) {
          setNoRule(true);
        } else {
          setNoRule(false);
          let newValues = initialValues;
          Object.keys(initialValues).forEach((key) => {
            newValues[key] = result.content[key];
          });

          setInitialValues({
            ...newValues,
          });
        }
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    supplierId && fetchResults(supplierId);
  }, [supplierId, enqueueSnackbar]);

  const requestWith = async () => {
    try {
      setLoading(true);
      await SupplierService.createPackSizeRule({ supplierId, payload: initialValues });
      enqueueSnackbar('Parcel Rule Created', { variant: 'success' });

      navigate(`/app/supplier/edit/${supplierId}`);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const updateDelivery = async () => {
    try {
      setLoading(true);
      await SupplierService.editPackSizeRuleById({ supplierId, payload: initialValues });

      enqueueSnackbar('Supplier Updated', { variant: 'success' });

      navigate(`/app/supplier/edit/${supplierId}`);
    } catch (error) {
      enqueueSnackbar(error.toString(), { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async () => {
    noRule ? requestWith() : updateDelivery();
  };

  const handleCancel = () => {
    navigate(`/app/supplier/edit/${supplierId}`);
  };

  const renderContent = () => (
    <Grid container>
      <Grid item container spacing={2} xs={5}>
        {Object.keys(initialValues).map((key, index) => (
          <Grid container item spacing={2} xs={12} key={index}>
            <Grid item xs={2}>
              <Typography style={{ textAlign: 'center' }}>{key} :</Typography>
            </Grid>
            <Grid item xs={5}>
              <TextField
                placeholder={`${key} min weight`}
                variant="outlined"
                value={initialValues[key][0]}
                onChange={(e) =>
                  setInitialValues({
                    ...initialValues,
                    [key]: [Number(e.target.value), initialValues[key][1]],
                  })
                }
              />
            </Grid>
            <Grid item xs={5}>
              <TextField
                placeholder={`${key} max weight`}
                variant="outlined"
                value={initialValues[key][1]}
                onChange={(e) =>
                  setInitialValues({
                    ...initialValues,
                    [key]: [initialValues[key][0], Number(e.target.value)],
                  })
                }
              />
            </Grid>
          </Grid>
        ))}

        <Box my={2} mx={'auto'} className={classes.buttons}>
          <Button
            color="primary"
            disabled={loading}
            size="large"
            onClick={handleSubmit}
            variant="contained"
          >
            {noRule ? 'Create' : 'Save'}
          </Button>
          <Button
            disabled={loading}
            size="large"
            variant="contained"
            type="reset"
            onClick={handleCancel}
          >
            Cancel
          </Button>
        </Box>
      </Grid>
    </Grid>
  );

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton
          title={supplierId ? 'Edit Supplier Package Rule' : 'Create Supplier Package Rule'}
        />
      </Box>
      <Card>
        <CardContent>{renderContent()}</CardContent>
      </Card>
    </Box>
  );
};

export default CreateDelivery;
