import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Grid, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton, StatusButton } from 'src/views/utils/Buttons';
import DriverService from 'src/services/driver';
import { SettingService } from 'src/services/setting';
import GeneralTable from 'src/views/utils/GeneralTable';
import Toolbar from './Toolbar';
import moment from 'moment';

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { currentPage = 1, driverId } = useParams();

  const [limit, setLimit] = useState(50);
  const [parcelList, setParcelList] = useState([]);
  const [parcelDetail, setParcelDetail] = useState({});
  const [count, setCount] = useState(0);

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  const [searchText, setSearchText] = useState('');

  useEffect(() => {
    const fetchResults = async () => {
      setParcelList(null);
      setLoading(true);
      try {
        const [result, parcelRes] = await Promise.all([
          DriverService.getjobdetailStatisticsByDriverId({
            driverId,
            numberPerPage: limit,
            currentPage: currentPage,
            size: searchText !== 'ALL' && searchText,
          }),
          DriverService.findDriverById(driverId),
        ]);

        setCount(result.content.numberOfResults);
        setParcelList(result.content.results);
        setParcelDetail(parcelRes.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [currentPage, limit, searchText, refreshFlag, enqueueSnackbar]);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    navigate(`/app/parcelSearch/${driverId}/${newPage + 1}`);
  };

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  const onSearch = (size) => {
    setSearchText(size);
  };

  const tableTitle = ['DAY', 'TOTAL', 'ATT', 'DELIVERY'];

  const handleExportExcel = () => {
    let rows = [];
    rows.push(tableTitle);
    parcelList.forEach((item) => rows.push([item.day, item.total, item.att, item.delivery]));

    let csvContent = rows.map((e) => e.join(',')).join('\n');
    let excel_file = document.createElement('a');
    excel_file.setAttribute(
      'href',
      'data:application/vnd.ms-excel;charset=utf-8,' + encodeURIComponent(csvContent),
    );
    excel_file.setAttribute('download', `${parcelDetail.name}_statistics.csv`);
    document.body.appendChild(excel_file);
    excel_file.click();
    document.body.removeChild(excel_file);
  };

  return (
    <Box>
      <Box mt={3}>
        <Toolbar onSearch={onSearch} />
        <Box mt={2} mb={2}>
          <Grid container spacing={2}>
            <Grid item xs={4}>
              name: {parcelDetail.name}
            </Grid>
            <Grid item xs={4}>
              phone: {parcelDetail.phone}
            </Grid>
            <Grid item xs={4}>
              email: {parcelDetail.email}
            </Grid>
          </Grid>
        </Box>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Parcel List" onRefresh={handleRefresh}>
              <MainOutLinedButton title="Export Result" onClick={handleExportExcel} />
            </BaseTableHeader>
          }
          headers={tableTitle}
          data={parcelList}
          handleLimitChange={handleLimitChange}
          handlePageChange={handlePageChange}
          currentPage={parseInt(currentPage) - 1}
          limit={limit}
          count={count}
          loading={loading}
        >
          {parcelList?.map((parcel, index) => (
            <TableRow hover key={index}>
              <TableCell>{parcel.day}</TableCell>
              <TableCell>{parcel.total}</TableCell>
              <TableCell>{parcel.att}</TableCell>
              <TableCell>{parcel.delivery}</TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
