import { authGet, authPost, authDelete, authUpdate } from './tools';

class DriverService {
  static getAllDriver = async ({ currentPage, numberPerPage, name, email }) => {
    const params = {
      currentPage,
      numberPerPage,
      name,
      email,
    };

    const res = await authPost('platform/driver/findDrivers', params);

    return res.data;
  };

  //Get a specific driver.
  static findDriverById = async (id) => {
    const res = await authGet(`platform/driver/findDriverById/${id}`);
    return res.data;
  };

  //Create a driver.
  static createDriver = async (params) => {
    const res = await authPost('platform/driver/createDriver', params);
    return res.data;
  };

  //Update a driver.
  static editDriver = async (driverId, payload) => {
    const res = await authUpdate(`platform/driver/editDriver/${driverId}`, payload);
    return res.data;
  };

  //Delete a named driver.
  static deleteDriver = async (id) => {
    const res = await authDelete(`platform/driver/deleteDriver/${id}`);

    return res.data;
  };

  //get Status By DriverId
  static getStatusByDriverId = async (driverId) => {
    const payload = { driverId };

    const res = await authPost('platform/job/getStatusByDriverId', payload);

    return res.data;
  };

  //Get Job Detail Statistics By DriverId
  static getjobdetailStatisticsByDriverId = async ({
    driverId,
    numberPerPage,
    currentPage,
    size,
  }) => {
    const payload = { driverId, numberPerPage, currentPage, size };

    const res = await authPost('platform/job/getjobdetailStatisticsByDriverId', payload);

    return res.data;
  };
}

export default DriverService;
