import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useNavigate, useParams } from 'react-router';
import { useSnackbar } from 'notistack';
import { Box, Grid, TableCell, TableRow, Typography } from '@material-ui/core';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import MainOutLinedButton, { NoBorderButton, StatusButton } from 'src/views/utils/Buttons';
import DriverService from 'src/services/driver';
import GeneralTable from 'src/views/utils/GeneralTable';
import moment from 'moment';

const Results = () => {
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  const { driverId } = useParams();
  const [driverList, setDriverList] = useState([]);
  const [driverDetail, setDriverDetail] = useState({});

  const [loading, setLoading] = useState(false);
  const [refreshFlag, setRefreshFlag] = useState(false);

  useEffect(() => {
    const fetchResults = async () => {
      setDriverList(null);
      setLoading(true);
      try {
        const [result, driverRes] = await Promise.all([
          DriverService.getStatusByDriverId(driverId),
          DriverService.findDriverById(driverId),
        ]);

        setDriverList(result.content);
        setDriverDetail(driverRes.content);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    fetchResults();
  }, [refreshFlag, enqueueSnackbar]);

  const handleRefresh = () => setRefreshFlag(!refreshFlag);

  let tableTitle = [
    'ID',
    'CONSIGNMENT NUMBER',
    'ONBAORD DATE',
    'COMPLETE DATE',
    'RECEIVER',
    'PHONE',
    'ADDRESS',
    'SUBURB',
    'POSTCODE',
    'STATE',
    'DISTANCE',
    'EST.TIME',
    'STATUS',
  ];

  const handleExportExcel = () => {
    let rows = [];
    rows.push(tableTitle);
    driverList.forEach((driver) =>
      rows.push([
        driver._id,
        driver.consignmentNumber,
        driver.onboardDate,
        driver.completeDate,
        driver.receiver,
        driver.phone,
        driver.address1,
        driver.suburb,
        driver.postcode,
        driver.state,
        driver.distance,
        driver.EstTime,
        driver.status,
      ]),
    );
    let csvContent = rows.map((e) => e.join(',')).join('\n');
    let excel_file = document.createElement('a');
    excel_file.setAttribute(
      'href',
      'data:application/vnd.ms-excel;charset=utf-8,' + encodeURIComponent(csvContent),
    );
    excel_file.setAttribute('download', `${driverDetail.name}_status.csv`);
    document.body.appendChild(excel_file);
    excel_file.click();
    document.body.removeChild(excel_file);
  };

  return (
    <Box>
      <Box mt={3}>
        <Box mt={2} mb={2}>
          <Grid container spacing={2}>
            <Grid item xs={4}>
              name: {driverDetail.name}
            </Grid>
            <Grid item xs={4}>
              phone: {driverDetail.phone}
            </Grid>
            <Grid item xs={4}>
              email: {driverDetail.email}
            </Grid>
          </Grid>
        </Box>
        <GeneralTable
          baseTableHeader={
            <BaseTableHeader title="Status List" onRefresh={handleRefresh}>
              <MainOutLinedButton title="Export Result" onClick={handleExportExcel} />
            </BaseTableHeader>
          }
          headers={[...tableTitle, 'ACTION']}
          data={driverList}
          loading={loading}
          noTablePagination={true}
        >
          {driverList?.map((driver) => (
            <TableRow hover key={driver._id}>
              <TableCell>{driver._id}</TableCell>
              <TableCell>{driver.consignmentNumber}</TableCell>
              <TableCell>{moment(driver.onboardDate).format('MM-DD-YYYY')}</TableCell>
              <TableCell>
                {driver.completeDate && moment(driver.completeDate).format('MM-DD-YYYY')}
              </TableCell>
              <TableCell>{driver.receiver}</TableCell>
              <TableCell>{driver.phone}</TableCell>
              <TableCell>{driver.address1}</TableCell>
              <TableCell>{driver.suburb}</TableCell>
              <TableCell>{driver.postcode}</TableCell>
              <TableCell>{driver.state}</TableCell>
              <TableCell>{driver.distance}</TableCell>
              <TableCell>{driver.EstTime}</TableCell>
              <TableCell>{driver.status}</TableCell>
              <TableCell>
                {driver.status !== 'D' && <StatusButton time={driver.onboardDate} />}
              </TableCell>
            </TableRow>
          ))}
        </GeneralTable>
      </Box>
    </Box>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  // customers: PropTypes.array.isRequired,
};

export default Results;
