import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, TextField, Grid } from '@material-ui/core';
import { SearchTextField } from 'src/views/utils/TextFields';
import GeneralSearchView from 'src/views/utils/GeneralSearchView';
import Select from 'src/views/utils/Select';

//TODO: REMOVE THIS REPEAT COMPONENT

const useStyles = makeStyles((theme) => ({
  root: {},
}));

const Toolbar = ({
  className,
  title,
  onSearch,
  onRefresh,
  disabled = false,
  placeholder = 'Numbers',
  hideSearchText = false,
  ...rest
}) => {
  const classes = useStyles();

  const onSearchClicked = () => {
    onSearch(size);
  };

  const sizeList = ['ALL', 'S', 'M', 'L', 'XL', 'XXL', 'XXXL'];

  const [size, setSize] = useState('');

  const onSelect = (e) => {
    setSize(e.target.value);
  };

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <GeneralSearchView onSearch={onSearchClicked} disabled={disabled}>
        <Grid item xs={4}>
          <Select
            values={size}
            text="please select parcel size"
            label="size"
            margin="normal"
            variant="outlined"
            className={classes.fullWidth}
            list={sizeList}
            handleChange={onSelect}
          />
        </Grid>
      </GeneralSearchView>
    </div>
  );
};

Toolbar.propTypes = {
  className: PropTypes.string,
};

export default Toolbar;
